import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { DemoComponent } from './demo/demo.component';

const routes: Routes = [
  {
    path: '',
    component: DemoComponent,
    children: [
      {
        path: '',
        pathMatch: 'full',
        loadChildren: () => import('./demo-categories').then(m => m.DemoCategoriesModule),
      },
      {
        path: 'gallery',
        loadChildren: () => import('./gallery').then(m => m.GalleryModule),
      },
      {
        path: 'posts',
        loadChildren: () => import('./posts').then(m => m.PostsModule),
      },
      {
        path: 'screen-share',
        loadChildren: () => import('./screen-share').then(m => m.ScreenShareModule),
      },
      {
        path: 'rtc',
        loadChildren: () => import('./rtc').then(m => m.RtcModule),
      },
      {
        path: 'payments',
        loadChildren: () => import('./payments').then(m => m.PaymentsModule),
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class DemoRoutingModule {}
